import axios from "axios";
import React from "react";

import ListAyat from "../../komponen/listAyat";

import Nav from "../../komponen/nav";
import { useParams } from "react-router-dom";
import Header from "../../komponen/header";

export default function AllAyat() {
  let { slug } = useParams();
  const [allAyat, SetAllAyat] = React.useState([]);
  const allAyatHandle = async () => {
    const result = await axios.get(
      `https://al-quran-8d642.firebaseio.com/surat/${slug}.json?print=pretty`
    );
    try {
      console.log(result);
      SetAllAyat(result.data);
    } catch (error) {
      console.log("error");
    }
  };
  allAyatHandle();

  return (
    <div className="h-screen w-full flex justify-center bg-gray-300">
      <div className="lg:w-2/5 bg-green-600    w-5/6 pt-10">
        <div className="h-2/6 w-full grid grid-cols-1 gap-2  px-7     ">
          <Header></Header>
          <div>
            <h1 className="text-3xl ml-4 font-bold">Al - Qur'an</h1>
          </div>
          <div >
            <Nav></Nav>
          </div>
        </div>
        <div className="h-4/6 w-full overflow-y-scroll gap-y-24 bg-white rounded-t-3xl px-8 py-2 ">
          {allAyat.map((ayat, index) => (
            <ListAyat ar={ayat.ar} nomor={ayat.nomor} id={ayat.id}></ListAyat>
          ))}
        </div>
      </div>
    </div>
  );
}
