import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom'
import React from 'react'
import Allsurah from './pages/allsurah'
import Surah from './pages/surah'



function App() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path='/alquran' exact>
         <Allsurah></Allsurah>
        </Route>
        <Route path='/alquran/surah/:slug'>
          <Surah></Surah>
        </Route>
        <Redirect from='/' to='/alquran' />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
