import React from "react";
import { useHistory } from "react-router-dom";


export default function ListSurat(props) {
  let history = useHistory();
  return (
    <React.Fragment>
      <div
        onClick={() => {
          history.push(`alquran/surah/${props.nomor}`);
        }}
        className="my-3   "
      >
        <div className="grid grid-cols-3 h-10">
          <div className="flex items-center w-60 grid-cols-4 ">
            <div className="border bg-blue-200 font-bold rounded-xl py-2 w-10 flex justify-center col-start-1 ">
              {props.nomor}
            </div>
            <div className="ml-5 w-40 col-start-2 col-span-3 ">
              <h1 className="font-bold">{props.nama}</h1>
              <p className="capitalize">{props.type}, <span>{props.ayat}</span> Ayat</p>
            </div>
          </div>
          <div className="flex justify-end items-center col-start-3">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-5 w-5"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z" />
            </svg>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
