import axios from "axios";
import React from "react";
import Header from "../../komponen/header";
import ListSurat from "../../komponen/listSurat";
import Nav from "../../komponen/nav";

export default function Allsurah() {
  const [allSurah, SetAllSurah] = React.useState([]);
  const allSurahHandle = async () => {
    const result = await axios.get(
      `https://al-quran-8d642.firebaseio.com/data.json?print=pretty`
    );
    try {
      
      SetAllSurah(result.data);
    } catch (error) {
      console.log('error')
    }
  };
  React.useEffect(() => {
    allSurahHandle();
  });
  return (
    <div className="h-screen w-full flex justify-center bg-gray-300">
      <div className="lg:w-2/5 bg bg-blue-100 w-5/6 pt-10 px-5 ">
        <div className="h-1/6 w-full grid grid-cols-1 gap-2">
          <Header></Header>
          <div>
            <h1 className="text-3xl ml-4 font-bold">Al - Qur'an</h1>
          </div>
          <div>
            <Nav></Nav>
          </div>
        </div>
        <div className="h-5/6 w-full grid grid-cols-1 overflow-y-auto  ">
            <div>
            {allSurah?.map((surah, index) =>(
              <ListSurat
                nomor={surah.nomor}
                nama={surah.nama}
                type={surah.type}
                ayat = {surah.ayat}
              ></ListSurat>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
